import { Client } from "../../client/metier/client.metier";

export class Reparation {
  id: number;
  date: Date;
  client: Client;
  montant: number;
  facturee: boolean;
  payee: boolean;
  typeReparation: string;

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }
}



