import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {ReparationRoutes} from './reparation.routing';
import {ReparationService} from "./service/reparation.service";
import {ReparationAddComponent} from './reparation.add/reparation.add.component';
import {ReparationListComponent} from "./reparation.list/reparation.list.component";
import {ReparationEditComponent} from "./reparation.edit/reparation.edit.component";
import {DataTableModule} from "angular2-datatable";
import {ClientService} from "../client/service/client.service";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(ReparationRoutes)
  ],
  declarations: [
    ReparationAddComponent,
    ReparationListComponent,
    ReparationEditComponent,
  ],
  providers: [
    ReparationService,
    ClientService,
  ],
})

export class ReparationModule {
}



