import {Routes} from '@angular/router';

import {ReparationAddComponent}  from './reparation.add/reparation.add.component';
import {ReparationListComponent} from "./reparation.list/reparation.list.component";
import {ReparationEditComponent} from "./reparation.edit/reparation.edit.component";

export const ReparationRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: ReparationAddComponent
  },
    {path: 'list', component: ReparationListComponent},
    {path: 'edit/:id', component: ReparationEditComponent},

  ]
}];



