import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { PageTitleService } from '../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { Reparation } from "../metier/reparation.metier";
import { ReparationService } from "../service/reparation.service";
import { BaseComponent } from "../../core/component/base-component";
import { Params, ActivatedRoute } from "@angular/router";
import { Client } from "../../client/metier/client.metier";
import { ClientService } from "../../client/service/client.service";
import { CustomValidators } from "ng2-validation";


@Component({
  selector: 'ms-reparation-edit',
  templateUrl: './reparation.edit.component.html',
  styleUrls: ['./reparation.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ReparationEditComponent extends BaseComponent implements OnInit {

  private reparation: Reparation;
  public reparationForm: FormGroup;
  private clientList: Array<Client> = [];
  private typeReparationList: string[] = [];


  constructor(private fb: FormBuilder,
    private route: ActivatedRoute,
    private pageTitleService: PageTitleService,
    private reparationService: ReparationService,
    private clientService: ClientService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("MODIFIER UNE REPARATION");
    this.loadReparation();
    this.reparationForm = this.fb.group({
      id: [Validators.required],
      date: [new Date().toISOString().substring(0, 10), Validators.compose([Validators.required, CustomValidators.date])],
      client: ['', Validators.required],
      typeReparation: ['', Validators.required],
    });
  }

  updateReparation(reparationForm: any) {
    this.reparation = new Reparation(reparationForm);
    this.reparationService.saveReparation(this.reparation)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/reparation/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  loadReparation() {
    this.route.params.switchMap((params: Params) => this.reparationService.getReparation(+params['id']))
      .subscribe(reparation => {
        this.reparation = reparation;
        this.reparationForm.patchValue(this.reparation);
        this.loadClientList();
        this.loadTypeReparationList();

        if (this.reparation.facturee === true) {
          this.reparationForm.get('date').disable();
          this.reparationForm.get('client').disable();
          this.reparationForm.get('typeReparation').disable();
        }

      }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteReparation(reparationForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.reparation = new Reparation(reparationForm);
    this.reparationService.deleteReparation(this.reparation)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/reparation/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  private loadClientList() {
    this.clientService.getClientList()
      .subscribe(clientList => {
        this.clientList = clientList;
        this.reparation.client = this.clientList.find(client => client.id === this.reparation.client.id);
        this.reparationForm.patchValue({ client: this.reparation.client }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadTypeReparationList() {
    this.reparationService.getTypeReparationList()
      .subscribe(typeReparationList => {
        this.typeReparationList = typeReparationList;
        this.reparationForm.patchValue({ typeReparation: this.reparation.typeReparation }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private isDisabled() {
    if (this.reparation != null) {
      if (this.reparation.facturee === true) {
        return true;
      } else {
        return false;
      }
    }
  }

  chiffrageRedirect() {
    this.router.navigate(['/chiffrage/list'], { queryParams: { reparation_id: this.reparation.id } });
  }


}






